.symbol-group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 10px;
}
.symbol-group .symbol {
  position: relative;
  z-index: 0;
  margin-left: -10px;
  border: 2px solid #ffffff;
  transition: all 0.3s ease;
}
.symbol-group .symbol:hover {
  transition: all 0.3s ease;
  z-index: 1;
}
.symbol-group.symbol-hover .symbol {
  cursor: pointer;
}

.pulse {
  position: relative;
}
.pulse.pulse-white .pulse-ring {
  border-color: #ffffff;
}
.pulse.pulse-light .pulse-ring {
  border-color: #f5f8fa;
}
.pulse.pulse-primary .pulse-ring {
  border-color: #009ef7;
}
.pulse.pulse-secondary .pulse-ring {
  border-color: #e4e6ef;
}
.pulse.pulse-success .pulse-ring {
  border-color: #50cd89;
}
.pulse.pulse-info .pulse-ring {
  border-color: #7239ea;
}
.pulse.pulse-warning .pulse-ring {
  border-color: #ffc700;
}
.pulse.pulse-danger .pulse-ring {
  border-color: #f1416c;
}
.pulse.pulse-dark .pulse-ring {
  border-color: #181c32;
}

.pulse-ring {
  display: block;
  border-radius: 40px;
  height: 40px;
  width: 40px;
  position: absolute;
  animation: animation-pulse 3.5s ease-out;
  animation-iteration-count: infinite;
  opacity: 0;
  border-width: 3px;
  border-style: solid;
  border-color: #a1a5b7;
}

@keyframes animation-pulse {
  0% {
    -webkit-transform: scale(0.1, 0.1);
    opacity: 0;
  }
  60% {
    -webkit-transform: scale(0.1, 0.1);
    opacity: 0;
  }
  65% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.2);
    opacity: 0;
  }
}
.page-loading * {
  transition: none !important;
}

.page-loader {
  background: #ffffff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: none;
}
.page-loading .page-loader {
  display: flex;
  justify-content: center;
  align-items: center;
}

.scrolltop {
  position: fixed;
  display: none;
  cursor: pointer;
  z-index: 100;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  bottom: 40px;
  right: 20px;
  background-color: #009ef7;
  box-shadow: 0 0.5rem 1.5rem 0.5rem rgba(0, 0, 0, 0.075);
  opacity: 0;
  transition: color 0.2s ease, background-color 0.2s ease;
  border-radius: 0.475rem;
}
.scrolltop .svg-icon {
  color: #ffffff;
}
.scrolltop .svg-icon svg {
  height: 24px;
  width: 24px;
}
.scrolltop > i {
  font-size: 1.3rem;
  color: #ffffff;
}
.scrolltop:hover {
  background-color: #009ef7;
}
.scrolltop:hover > i {
  color: #ffffff;
}
.scrolltop:hover .svg-icon {
  color: #ffffff;
}
[data-kt-scrolltop='on'] .scrolltop {
  opacity: 0.3;
  animation: animation-scrolltop 0.4s ease-out 1;
  display: flex;
}
[data-kt-scrolltop='on'] .scrolltop:hover {
  transition: color 0.2s ease, background-color 0.2s ease;
  opacity: 1;
}
@media (max-width: 991.98px) {
  .scrolltop {
    bottom: 30px;
    right: 15px;
    width: 30px;
    height: 30px;
  }
}

